<template>
  <v-container grid-list-xs>
    <v-dialog
      transition="dialog-bottom-transition"
      max-width="700"
      v-model="messageDialog.enable"
    >
      <v-card>
        <v-toolbar
          :color="messageDialog.color"
          dark
          class="text-h5"
        >{{ messageDialog.title }}</v-toolbar>
        <v-card-text>
          <v-row>
            <v-col cols="1">
              <v-icon x-large class="pa-12">mdi-information-outline</v-icon>
            </v-col>
            <v-col cols="11">
              <div class="text-h5 pa-12">{{messageDialog.message}}</div>
            </v-col>
          </v-row>
        </v-card-text>
        <v-card-actions class="justify-end">
          <v-btn
            text
            @click="messageDialog.enable = false"
          >Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-stepper v-model="step">
      <v-stepper-header>
        <v-stepper-step
          :complete="step > 1"
          step="1"
        >
          Registration Checking
        </v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step
          :complete="step > 2"
          step="2"
        >
          Account Creation
        </v-stepper-step>
      </v-stepper-header>

      <v-stepper-items>
        <v-stepper-content step="1">
          <v-card
            class="mx-auto"
            max-width="700"
            outlined
            elevation="0"
          >
            <v-card-title primary-title>
              Provide below information to proceed
            </v-card-title>
            <v-card-text class="px-12 pt-6">
              <v-autocomplete
                label="ID Type*" 
                v-model="idType" 
                :items="idTypes"
                item-text="display"
                item-value="name"
                return-object
                outlined 
                hide-details="auto" 
                dense
                :rules="idtyperules"
                @change="errors.idtype = []"
              />
              <br v-if="idType">
              <v-text-field
                v-if="idType"
                :label="idType.display + ' Number*'"
                v-model="idNum" 
                outlined 
                hide-details="auto" 
                :rules="idnumrules"
                dense
              />
              <br>
              <v-text-field
                label="License Number*"
                v-model="licenseNum" 
                outlined 
                hide-details="auto" 
                :rules="licensenumrules"
                dense
              />
            </v-card-text>
          </v-card>
          <br>
          <v-spacer></v-spacer>
          <v-row>
            <v-spacer></v-spacer>
            <v-col cols="3">
              <v-btn color="primary" rounded @click="checkRecord">
                <v-icon left>
                  mdi-account-plus
                </v-icon>
                Submit
              </v-btn>
              &nbsp;
              <v-btn color="error" class="align-right" rounded @click="$router.push({path: '/'})">
                <v-icon left>
                  mdi-cancel
                </v-icon>
                Cancel
              </v-btn>
            </v-col>
          </v-row>
        </v-stepper-content>

        <v-stepper-content step="2">
          <v-card
            class="mx-auto"
            max-width="700"
            outlined
            elevation="0"
          >
            <v-card-text class="px-12 pt-6">
              <v-text-field
                label="Email*"
                v-model="email" 
                outlined 
                hide-details="auto" 
                :rules="emailrules"
                dense
              />
              <br>
              <v-menu
                v-model="birthDateMenu"
                :close-on-content-click="false"
                :nudge-right="40"
                transition="scale-transition"
                offset-y
                min-width="290px"
              >
                <template v-slot:activator="{ on }">
                  <v-text-field
                    v-model="birthDateFormatted"
                    label="Birth Date*"
                    prepend-inner-icon="mdi-calendar"
                    readonly
                    v-on="on"
                    :rules="birthdaterules"
                    outlined
                    hide-details="auto"
                    dense
                  />
                </template>
                <v-date-picker
                  :active-picker.sync="activePicker"
                  :max="maxDOBDate"
                  :min="minDOBDate"
                  color="secondary"
                  v-model="birthDate"
                  @change="birthDateFormatted = formatDate(birthDate)"
                  @input="birthDateMenu = false"
                />
              </v-menu>
            </v-card-text>
          </v-card>
          <br>
          <v-spacer></v-spacer>
          <v-row>
            <v-spacer></v-spacer>
            <v-col cols="3">
              <v-btn color="primary" @click="createAccount">
                <v-icon left>
                  mdi-account-plus
                </v-icon>
                Create
              </v-btn>
              &nbsp;
              <v-btn color="error" class="align-right" @click="$router.push({path: '/'})">
                <v-icon left>
                  mdi-cancel
                </v-icon>
                Cancel
              </v-btn>
            </v-col>
          </v-row>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>
  </v-container>
</template>
<script>
export default {
  data() {
    return {
      messageDialog: {
        enable: false,
        color: 'error',
        title: '',
        message: ''
      },
      step: 1,
      errors: {
        emails: [],
        birthdate: [],
        idtype: [],
        idnum: [],
        licensenum: []
      },
      idTypes: [{
        name: 'omang',
        display: 'OMANG'
      }, {
        name: 'passport',
        display: 'Passport'
      }],
      idType: "",
      idNum: "",
      licenseNum: "",
      email: "",
      birthDate: "",
      birthDateFormatted: '',
      birthDateMenu: false,
      activePicker: null
    }
  },
  watch: {
    birthDateMenu (val) {
      val && setTimeout(() => (this.activePicker = 'YEAR'))
    },
  },
  computed: {
    maxDOBDate() {
      return this.$moment().subtract(18, "YEARS").format("YYYY-MM-DD")
    },
    minDOBDate() {
      return this.$moment().subtract(100, "YEARS").format("YYYY-MM-DD")
    },
    idtyperules() {
      return [
        (v) => {
          if(!v) {
            this.errors.idtype = ["ID Type is required"]
            return "ID Type is required"
          }
          return true
        }
      ]
    },
    idnumrules() {
      return [
        (v) => {
          if(!v) {
            this.errors.idnum = ["ID Number is required"]
            return "ID Number is required"
          }
          return true
        }
      ]
    },
    licensenumrules() {
      return [
        (v) => {
          if(!v) {
            this.errors.licensenum = ["License Number is required"]
            return "License Number is required"
          }
          return true
        }
      ]
    },
    emailrules() {
      return [
        (v) => {
          if(!v) {
            this.errors.email = ["Email is required"]
            return "Email is required"
          }
          if(v.split("@").length !== 2 || (v.split("@").length === 2 && (!v.split("@")[0] || !v.split("@")[1]))) {
            this.errors.email = ["Invalid Email"]
            return "Invalid Email"
          }
          if(v.split(".").length <= 1 || (v.split(".").length > 1 && !v.split(".")[1])) {
            this.errors.email = ["Invalid Email"]
            return "Invalid Email"
          }
          return true
        }
      ]
    },
    birthdaterules() {
      return [
        (v) => {
          this.errors.birthdate = []
          if(!v) {
            this.errors.birthdate = ["Birth date is required"]
            return "Birth date is required"
          }
          return true
        }
      ]
    }
  },
  methods: {
    createAccount() {
      let opts = {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        redirect: 'manual',
        body: JSON.stringify({
          idType: this.idType.name,
          idNum: this.idNum,
          licenseNum: this.licenseNum,
          email: this.email,
          birthDate: this.birthDate
        })
      }
      fetch("/site/unAuthenticated/signup", opts).then((response) => {
        response.json().then(() => {
          
        }).catch(() => {
          this.messageDialog.color = "error"
          this.messageDialog.enable = true
          this.messageDialog.title = "Error"
          this.messageDialog.message = "Unexpected error has occured"
        })
      }).catch(() => {
        this.messageDialog.color = "error"
        this.messageDialog.enable = true
        this.messageDialog.title = "Error"
        this.messageDialog.message = "Unexpected error has occured"
      })
    },
    checkRecord() {
      let opts = {
        method: "POST",
        headers: {
          "Content-Type": "application/fhir+json"
        },
        redirect: 'manual',
        body: JSON.stringify({
          idType: this.idType.name,
          idNum: this.idNum,
          licenseNum: this.licenseNum
        })
      }
      fetch("/site/unAuthenticated/validateSignup", opts).then((response) => {
        response.json().then((response) => {
          if(response.type === "failed") {
            this.messageDialog.color = "error"
            this.messageDialog.enable = true
            this.messageDialog.title = response.title
            this.messageDialog.message = response.message
          } else {
            this.messageDialog.color = "success"
            this.step = 2
          }
        }).catch(() => {
          this.messageDialog.color = "error"
          this.messageDialog.enable = true
          this.messageDialog.title = "Error"
          this.messageDialog.message = "Unexpected error has occured"
        })
      }).catch(() => {
        this.messageDialog.color = "error"
        this.messageDialog.enable = true
        this.messageDialog.title = "Error"
        this.messageDialog.message = "Unexpected error has occured"
      })
    },
    formatDate(date) {
      if (!date) return null;
      const [year, month, day] = date.split("-");
      return `${day}-${month}-${year}`;
    },
  }
}
</script>
